import React from 'react'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Form from 'react-bootstrap/Form'
import InputGroup from 'react-bootstrap/InputGroup'
import Button from 'react-bootstrap/Button'
import Result from './Result'
import { Spinner } from 'react-bootstrap'
import Modal from 'react-bootstrap/Modal'

declare var gapi: any;

interface IProps {
}

interface IState {
  searching?: boolean;
  loaded?: boolean;
  raised: number;
  goal?: number;
  error?: boolean;
  showErrorModal?: boolean;
}

class AppMain extends React.Component<IProps, IState> {
  private lookup = {
    0: 500, // execs
    1: 80, // dancers
    2: 80,
    3: 80,
    4: 80,
    5: 50, // runners
    6: 120, // morale captains
  } 
  private input = React.createRef<HTMLInputElement>();

  constructor(props) {
    super(props);

    this.state = { searching: false, loaded: false, raised: 0};
    this.loadData = this.loadData.bind(this);
  }

  findTotal = (range, id): number | null => {
    //console.log(range)
    for (var i = 0; i < range.length; i++) {
      var donations = range[i]
      if(donations[0] === id) {
        return donations[5].replace('$', '') as unknown as number;
      }
    }
    return null
  }

  loadData = async (e) => {
    var found = false;
    const val = this.input.current
    if(val) {
      this.setState({searching: true, loaded: true})
      const id = val.value;
      gapi.client.sheets.spreadsheets.values.batchGet({
        spreadsheetId: '1vW68Wava--5BEglqM-MGovOuLH8LgPZNpFENAtOGDxo',
        ranges: ['Execs!D2:I', 'Freshmen(Dancers)!D2:I'],
      }).then(response => {
        this.setState({searching: false})
        var ranges = response.result.valueRanges;
        for(var i = 0; i < ranges.length; i++) {
          var total = this.findTotal(ranges[i].values, id);
          if(total) {
            found = true;
            this.setState({raised: total, goal: this.lookup[i]})
          }
        }
        if(!found) {
          this.setState({
            error: true,
            showErrorModal: true,
            loaded: false,
          })
        }
      })
    }
    //console.log(gapi);
    e.preventDefault()
  }


  render() {
    return (
      <Container> 
      <Row>
        <Col lg="12" className="text-center">
          <h1 className="mt-5">See how much you've raised!</h1>
          <p className="lead">Enter your student ID below.</p>
        </Col>
      </Row>
      <Form onSubmit={this.loadData} >
        <InputGroup className="mb3">
          <Form.Control as="input" pattern="\d{5}" maxLength="5" name="id" placeholder="Enter your student ID number here" autoComplete="off" ref={this.input as React.RefObject<any>}></Form.Control>
          <InputGroup.Append>
            <Button variant="outline-primary" type="submit" >
              {this.state.searching ? 
              <Spinner animation="border" role="status" variant="light"><span className="sr-only">Loading...</span></Spinner> : 
              "Search"}
            </Button>
          </InputGroup.Append>
        </InputGroup>
      </Form>
      <Result visible={this.state.loaded} raised={this.state.raised} goal={this.state.goal} />
      <br />
      <Row>
        <Col lg="12" className="text-center">
        <p>Please contact
            <a href="mailto:nnfinance@dm219.org"> Dance Marathon Finance </a>
          for any questions.</p>
        </Col>
      </Row>

      
      <Modal show={this.state.showErrorModal} onHide={() => this.setState({showErrorModal: false})}>
        <Modal.Header closeButton>
          <Modal.Title>Error</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p>
            Cannot find user in the Dance Marathon database with the given ID number. Please note the database may not be updated until
            Wednesday evening.
            
            <br /> <br />Please contact <a href="mailto:nnfinance@dm219.org">Dance Marathon Finance</a> for any questions.
          </p>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => this.setState({showErrorModal: false})}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    </Container>
  )
  }
}

export default AppMain